<template>
  <div>
    <el-form-item :label="$t('')">
      <l-select v-model="config.orderId" @change="handleOrderChange" :options="fields" />
    </el-form-item>
    <el-form-item v-if="config.table" :label="$t('')">
      <el-switch v-model="config.isDESC"></el-switch>
    </el-form-item>
    <el-form-item :label="$t('')">
      <el-switch v-model="config.required"></el-switch>
    </el-form-item>
    <el-form-item :label="$t('')">
      <el-switch v-model="config.isShowNum"></el-switch>
    </el-form-item>
    <el-form-item :label="$t('')">
      <el-switch v-model="config.isRowFixed"></el-switch>
    </el-form-item>
    <el-form-item :label="$t('')" v-if="config.isRowFixed">
      <el-input-number
        v-model="config.rowNum"
        controls-position="right"
        :placeholder="$t('')"
        :min="1"
      ></el-input-number>
    </el-form-item>
    <el-form-item v-if="!config.isRowFixed" :label="$t('')">
      <el-switch v-model="config.isAddBtn"></el-switch>
    </el-form-item>

    <el-form-item v-if="!config.isRowFixed" :label="$t('')">
      <el-switch v-model="config.isRemoveBtn"></el-switch>
    </el-form-item>
    <!--<el-form-item v-if="config.isAddBtn" :label="$t('')">
      <el-input v-model="config.addBtnText"></el-input>
    </el-form-item>-->

    <el-divider>{{ $t("") }}</el-divider>
    <div style="text-align: center; margin-bottom: 16px">
      <el-radio-group v-model="config.classType" size="mini">
        <el-radio-button label="1">{{ $t("") }}</el-radio-button>
        <el-radio-button label="2">{{ $t("") }}</el-radio-button>
      </el-radio-group>
    </div>
    <el-form-item
      v-if="config.classType == '1' && config.isRowFixed"
      :label="$t('')"
    >
      <el-switch v-model="config.isRowMerge"></el-switch>
    </el-form-item>
    <div
      v-if="
        config.isRowFixed &&
        config.isRowMerge &&
        config.classType == '1' &&
        config.mergeRows
      "
    >
      <div
        class="learun-row-form-input"
        :key="index"
        v-for="(item, index) in config.mergeRows"
      >
        <div class="flex">
          <l-select
            style="width: 60%"
            v-model="item.col"
            :placeholder="$t('')"
            :options="componentOptions"
          />
          <el-input-number
            v-model="item.row1"
            controls-position="right"
            :placeholder="$t('')"
            :min="1"
            :max="config.rowNum"
            size="mini"
            style="width: 40%"
          ></el-input-number>
          <el-input-number
            v-model="item.row2"
            controls-position="right"
            :placeholder="$t('')"
            :min="1"
            :max="config.rowNum"
            size="mini"
            style="width: 40%"
          ></el-input-number>
        </div>
        <el-button
          :title="$t('')"
          @click="handleRemoveMergeRows(index)"
          class="learun-row-form-input-delete"
          circle
          plain
          size="mini"
          type="danger"
        >
          <i class="el-icon-close"></i>
        </el-button>
      </div>
    </div>
    <div class="mt-8" v-if="config.isRowFixed">
      <el-button
        v-if="config.isRowMerge && config.classType == '1'"
        type="primary"
        size="mini"
        @click="handleAddMergeRows"
        >{{ $t("") }}</el-button
      >
      <el-button type="primary" size="mini" @click="handleDefault">{{
        $t("")
      }}</el-button>
    </div>

    <el-divider>{{ $t("") }}</el-divider>
    <template v-if="config.mergeHeader">
      <div
        class="reg-item"
        :key="index"
        v-for="(item, index) in config.mergeHeader"
      >
        <el-form-item
          label-width="48px"
          :label="$t('')"
          style="margin-bottom: 8px"
        >
          <el-input v-model="item.label" :placeholder="$t('')"></el-input>
        </el-form-item>
        <el-form-item
          label-width="48px"
          :label="$t('')"
          style="margin-bottom: 8px"
        >
          <l-select
            v-model="item.value"
            multiple
            :placeholder="$t('')"
            :options="getColumns(item.prop)"
          />
        </el-form-item>
        <el-form-item
          label-width="48px"
          :label="$t('')"
          style="margin-bottom: 0"
        >
          <l-select v-model="item.align" :options="alignOptions" />
        </el-form-item>

        <el-button
          :title="$t('')"
          @click="handleRemoveCol(index)"
          class="reg-item-delete"
          circle
          plain
          size="mini"
          type="danger"
        >
          <i class="el-icon-close"></i>
        </el-button>
      </div>
    </template>
    <div class="mt-8">
      <el-button type="primary" size="mini" @click="handleAddCol">{{
        $t("")
      }}</el-button>
    </div>
    <el-divider>{{ $t("") }}</el-divider>
    <div v-if="config.vRepeatList">
      <div
        class="learun-row-form-input"
        :key="index"
        v-for="(item, index) in config.vRepeatList"
      >
        <el-form-item label-width="40px" label="" style="margin-bottom: 0">
          <l-select
            multiple
            :options="vRepeatOptions(index)"
            v-model="item.value"
          />
        </el-form-item>
        <el-button
          :title="$t('')"
          @click="handleRemoveVRepeatList(index)"
          class="learun-row-form-input-delete"
          circle
          plain
          size="mini"
          type="danger"
        >
          <i class="el-icon-close"></i>
        </el-button>
      </div>
    </div>
    <div>
      <el-button
        class="mt-8"
        type="primary"
        size="mini"
        @click="handleAddVRepeatList"
        >{{ $t("") }}</el-button
      >
    </div>
    <el-divider v-if="!isNoScript">{{ $t("") }}</el-divider>
    <div class="mt-8" v-if="!isNoScript">
      <el-button
        @click="handleBtnClick('changeDataEvent', '')"
        type="primary"
        size="mini"
        ></el-button
      >
    </div>
    <l-dialog
      :title="formTitle"
      :visible.sync="formVisible"
      :height="528"
      :width="1000"
      @ok="handleCodeSave"
      @opened="handleOpenedForm"
    >
      <l-layout :right="400">
        <l-code-mirror
          v-model="code"
          isHint
          :handleHint="handleHint"
          mode="application/javascript"
        ></l-code-mirror>
        <template #right>
          <l-code-mirror readOnly v-model="tempCode"> </l-code-mirror>
        </template>
      </l-layout>
    </l-dialog>

    <!---->
    <l-dialog
      :title="$t('')"
      :height="528"
      :width="1000"
      :visible.sync="defaultFormVisible"
      @ok="handleDefaultSave"
      @opened="handleOpenedDefaultForm"
    >
      <gridtable-form ref="gridtableForm" :data="data" />
    </l-dialog>
  </div>
</template>

<script>
import GridtableForm from "./gridtableForm.vue";
export default {
  name: "config-gridtable",
  inject: ["formDesign"],
  components: {
    GridtableForm,
  },
  props: ["data"],
  data() {
    return {
      defaultFormVisible: false,

      formTitle: "",
      formVisible: false,
      code: "",
      type: "",
      alignOptions: [
        { value: "left", label: this.$t("") },
        { value: "center", label: this.$t("") },
        { value: "right", label: this.$t("") },
      ],
      tempCode: `
// ,ES5
// 
var isUpdate = learun.isUpdate

// id
var id = learun.prop
// 
var data = learun.data
//  rowIndex
var rowIndex = learun.rowIndex

// 
var value = learun.get('id')
// 
var childRow = learun.get('id.')
// 
var childCell = learun.get('id..id')

// 
learun.set({path:'id',value:'xxxx'})
// 
var row = {}
learun.set({path:'id',value:row,type:'addTable'})
// 
learun.set({path:'id.',type:'deleteTable'})
// 
learun.set({path:'id..id',value:'xxxxxx'})

// 
var label = learun.getLabel('id')
// 
var cellLabel =  learun.getLabel('id..id')

// 
learun.setRequired('id')
// 
learun.setRequired('id',false)

// 
learun.setDisabled('id')
// 
learun.setDisabled('id',false)

// 
learun.setHide('id')
// 
learun.setHide('id',false)

// 
learun.set({path:'id..hasNoDeleteBtn',value:false})
// 
learun.set({path:'id..disabled',value:true})
// 
learun.set({path:'id..disabled',value:true})
learun.set({path:'id..abledList',value:['id']})


// 
learun.loading('')
// 
learun.hideLoading('')

// 
learun.message('')

// 
var loginUser = learun.loginUser

// get
// 
var getCallback = function(res){ console.log(res)// }
learun.httpGet({url:api,params:{},getCallback})

// post
// 
var postCallback = function(res){ console.log(res)// }
learun.httpPost({url:api,params:{},data:{},postCallback})

// put
// 
var putCallback = function(res){ console.log(res)// }
learun.httpPut({url:api,params:{},data:{},putCallback})

// post
// 
var deleteCallback = function(res){ console.log(res)// }
learun.httpDelete({url:api,params:{},data:{},deleteCallback})

// 
return 'callback'

            `,
    };
  },
  computed: {
    config() {
      return this.data;
    },
    isNoScript() {
      return this.formDesign.isNoScript;
    },
    fields() {
      const table =
        this.formDesign.dbTables.find((t) => t.name == this.config.table) || {};
      return (table.columns || []).map((t) => {
        return {
          label: t.coment ? `${t.name}(${t.coment})` : t.name,
          value: t.name,
          csType:t.csType
        };
      });
    },
    componentOptions() {
      return this.config.children.map((t) => ({
        label: t.label,
        value: t.prop,
      }));
    },
    columns() {
      const res = [];

      if (this.config.mergeHeader) {
        this.config.mergeHeader.forEach((t) => {
          res.push({ label: t.label, value: t.prop });
        });
      }
      res.push(
        ...this.config.children.map((t) => ({ label: t.label, value: t.prop }))
      );

      return res;
    },
  },
  methods: {
    handleBtnClick(type, title) {
      this.type = type;
      this.formTitle = title;
      this.formVisible = true;
    },
    getEvent(strEvent) {
      // 
      if (!this.$validatenull(strEvent)) {
        if (strEvent.indexOf("=>") != -1) {
          // 
          console.warn("ES6ES5");
          return { res: false, msg: "" };
        } else {
          strEvent = `(function(){function fn(learun) {${strEvent}} return fn})()`;
          return this.$getFunction(strEvent);
        }
      } else {
        return { res: true };
      }
    },
    handleCodeSave() {
      const { res, msg } = this.getEvent(this.code);
      if (res) {
        this.data[this.type] = this.code;
        this.formVisible = false;
      } else {
        this.$message({
          type: "error",
          message: `${this.$t("")}:${msg}`,
        });
      }
    },
    handleOpenedForm() {
      this.code = this.data[this.type] || "";
    },
    handleHint(text) {
      text = text.replace(/'/g, "");
      text = text.replace(/"/g, "");
      const myList = this.formDesign.componentList.filter(
        (t) =>
          t.pprop == this.config.prop &&
          (t.label.indexOf(text) != -1 || t.prop.indexOf(text) != -1)
      );
      const list = this.formDesign.componentList.filter(
        (t) =>
          t.pprop != this.config.prop &&
          (t.label.indexOf(text) != -1 || t.prop.indexOf(text) != -1)
      );

      myList.push(...list);
      return myList.map((t) => `/*${t.label}*/"${t.prop}"`);
    },

    handleOrderChange(obj) {
      if (obj) {
        this.config.orderCsType = obj.csType;
      } else {
        this.config.orderCsType = "";
      }
    },

    // 
    vRepeatOptions(index) {
      let selectValue = "";
      if (this.config.vRepeatList) {
        this.config.vRepeatList.forEach((t, i) => {
          if (t.value && i != index) {
            selectValue += `${t.value},`;
          }
        });
      }
      return this.componentOptions.filter(
        (t) => selectValue.indexOf(t.value) == -1
      );
    },
    handleAddVRepeatList() {
      // 
      if (this.config.vRepeatList == undefined) {
        this.$set(this.config, "vRepeatList", []);
      }
      this.config.vRepeatList.push({
        value: "",
      });
    },
    handleRemoveVRepeatList(index) {
      this.config.vRepeatList.splice(index, 1);
    },

    // 
    getColumns(prop) {
      let selectValue = "";
      if (this.config.mergeHeader) {
        this.config.mergeHeader.forEach((t) => {
          if (t.value && t.prop != prop) {
            selectValue += `${t.value},`;
          }
        });
      }
      return this.columns.filter(
        (t) => t.value != prop && selectValue.indexOf(t.value) == -1
      );
    },
    handleAddCol() {
      if (this.config.mergeHeader == undefined) {
        this.$set(this.config, "mergeHeader", []);
      }
      this.config.mergeHeader.push({
        label: `${this.config.mergeHeader.length}`,
        prop: this.$uuid(),
        value: "",
        align: "left",
      });
    },
    handleRemoveCol(index) {
      this.config.mergeHeader.splice(index, 1);
    },

    // 
    handleDefault() {
      this.defaultFormVisible = true;
    },
    handleOpenedDefaultForm() {
      const dataSource = [];
      for (let i = 0; i < this.config.rowNum; i++) {
        const row = this.config.dataSource[i];
        const point = {};
        this.config.children.forEach((col) => {
          if (row) {
            point[col.prop] = row[col.prop] || col.default || undefined;
          } else {
            point[col.prop] = col.default || undefined;
          }
        });
        dataSource.push(point);
      }
      this.$refs.gridtableForm.setData(dataSource);
    },
    handleDefaultSave() {
      this.config.dataSource = this.$refs.gridtableForm.getData();
      this.defaultFormVisible = false
    },

    // 
    handleAddMergeRows() {
      if (this.config.mergeRows == undefined) {
        this.$set(this.config, "mergeRows", []);
      }
      this.config.mergeRows.push({
        col: "",
        row1: undefined,
        row2: undefined,
      });
    },
    handleRemoveMergeRows(index) {
      this.config.mergeRows.splice(index, 1);
    },
  },
};
</script>

