<template>
  <div class="form-config-container">
    <el-form label-position="left" label-width="80px" size="mini">
      <el-form-item :label="$t('')">
        <el-radio-group v-model="formInfo.size">
          <el-radio-button label="medium">{{ $t("") }}</el-radio-button>
          <el-radio-button label="small">{{ $t("") }}</el-radio-button>
          <el-radio-button label="mini">{{ $t("") }}</el-radio-button>
        </el-radio-group>
      </el-form-item>
      <el-form-item :label="$t('')">
        <el-radio-group v-model="formInfo.labelPosition">
          <el-radio-button label="right">{{ $t("") }}</el-radio-button>
          <el-radio-button label="left">{{ $t("") }}</el-radio-button>
          <el-radio-button label="top">{{ $t("") }}</el-radio-button>
        </el-radio-group>
      </el-form-item>
      <el-form-item :label="$t('')">
        <el-input-number
          v-model="formInfo.labelWidth"
          controls-position="right"
          :placeholder="$t('')"
          :min="0"
        ></el-input-number>
      </el-form-item>
      <el-form-item :label="$t('')">
        <el-input-number
          v-model="formInfo.gutter"
          controls-position="right"
          :placeholder="$t('')"
          :min="0"
        ></el-input-number>
      </el-form-item>
      <el-form-item :label="$t('')">
        <l-select
          v-model="formInfo.openType"
          :options="[
            { value: '1', label: $t('') },
            { value: '2', label: $t('') },
          ]"
        ></l-select>
      </el-form-item>
      <el-form-item :label="$t('')" v-if="formInfo.openType == '1'">
        <el-input-number
          v-model="formInfo.dialogWidth"
          controls-position="right"
          :placeholder="$t('')"
          :min="0"
        ></el-input-number>
      </el-form-item>
      <el-form-item :label="$t('')" v-if="formInfo.openType == '1'">
        <el-input-number
          v-model="formInfo.dialogHeight"
          controls-position="right"
          :placeholder="$t('')"
          :min="0"
        ></el-input-number>
      </el-form-item>
      <el-form-item :label="$t('')" v-if="formInfo.openType == '2'">
        <el-input-number
          v-model="formInfo.drawerWidth"
          controls-position="right"
          :placeholder="$t('')"
          :min="0"
        ></el-input-number>
      </el-form-item>
      <el-form-item :label="$t('')">
        <el-radio-group v-model="formInfo.historyType">
          <el-radio-button label="0">{{ $t("") }}</el-radio-button>
          <el-radio-button label="1">{{ $t("") }}</el-radio-button>
          <el-radio-button label="2">{{ $t("") }}</el-radio-button>
        </el-radio-group>
      </el-form-item>
      <el-divider>{{ $t("") }}</el-divider>
      <div v-if="formInfo.vRepeatList">
        <div
          class="learun-row-form-input"
          :key="index"
          v-for="(item, index) in formInfo.vRepeatList"
        >
          <el-form-item
            label-width="40px"
            label=""
            style="margin-bottom: 0"
          >
            <l-select
              multiple
              :options="vRepeatOptions(index)"
              v-model="item.value"
            />
          </el-form-item>
          <el-button
            :title="$t('')"
            @click="handleRemoveVRepeatList(index)"
            class="learun-row-form-input-delete"
            circle
            plain
            size="mini"
            type="danger"
          >
            <i class="el-icon-close"></i>
          </el-button>
        </div>
      </div>
      <div>
        <el-button
          class="mt-8"
          type="primary"
          size="mini"
          @click="handleAddVRepeatList"
          >{{ $t("") }}</el-button
        >
      </div>
      <el-divider>{{ $t("") }}</el-divider>
      <draggable
        :list="tabList"
        :group="{ name: 'dic' }"
        ghost-class="set-item-ghost"
        handle=".drag-item"
      >
        <div v-for="(item, index) in tabList" class="set-item" :key="index">
          <i class="drag-item el-icon-rank"></i>
          <el-input
            size="mini"
            v-model="item.text"
            :placeholder="$t('')"
          ></el-input>
          <el-button
            v-if="tabList.length > 1"
            @click="handleRemoveTabs(index)"
            circle
            plain
            type="danger"
            size="mini"
            icon="el-icon-minus"
            class="delete-item"
            style="padding: 4px"
          ></el-button>
        </div>
      </draggable>
      <div style="padding-left: 22px">
        <el-button
          size="mini"
          type="text"
          icon="el-icon-circle-plus-outline"
          @click="handleAddTabs"
          >{{ $t("") }}</el-button
        >
      </div>

      <template v-if="!isNoScript">
        <el-divider>{{ $t("") }}</el-divider>
        <div class="mt-8">
          <el-button
            @click="handleBtnClick('beforeSetData', '')"
            type="primary"
            size="mini"
            ></el-button
          >
          <el-button
            @click="handleBtnClick('afterValidateForm', '')"
            type="primary"
            size="mini"
            ></el-button
          >
        </div>
        <div class="mt-8">
          <el-button
            @click="handleBtnClick('afterSaveEvent', '')"
            type="primary"
            size="mini"
            ></el-button
          >
          <el-button
            @click="handleBtnClick('changeDataEvent', '')"
            type="primary"
            size="mini"
            ></el-button
          >
        </div>
      </template>
    </el-form>
    <l-dialog
      :title="formTitle"
      :visible.sync="formVisible"
      :height="528"
      :width="1000"
      @ok="handleCodeSave"
      @opened="handleOpenedForm"
    >
      <l-layout :right="400">
        <l-code-mirror
          v-model="code"
          isHint
          :handleHint="handleHint"
          mode="application/javascript"
        ></l-code-mirror>
        <template #right>
          <l-code-mirror readOnly v-model="tempCode"> </l-code-mirror>
        </template>
      </l-layout>
    </l-dialog>
  </div>
</template>

<script>
export default {
  name: "form-config",
  components: {},
  inject: ["formDesign"],
  data() {
    return {
      formTitle: "",
      formVisible: false,
      code: "",
      type: "",
      tempCode: `
// ,ES5
// 
var isUpdate = learun.isUpdate

// id
var id = learun.prop
// 
var data = learun.data
//  rowIndex
var rowIndex = learun.rowIndex

// 
var value = learun.get('id')
// 
var childRow = learun.get('id.')
// 
var childCell = learun.get('id..id')

// 
learun.set({path:'id',value:'xxxx'})
// 
var row = {}
learun.set({path:'id',value:row,type:'addTable'})
// 
learun.set({path:'id.',type:'deleteTable'})
// 
learun.set({path:'id..id',value:'xxxxxx'})

// 
var label = learun.getLabel('id')
// 
var cellLabel =  learun.getLabel('id..id')

// 
learun.setRequired('id')
// 
learun.setRequired('id',false)

// 
learun.setDisabled('id')
// 
learun.setDisabled('id',false)

// 
learun.setHide('id')
// 
learun.setHide('id',false)

// 
learun.set({path:'id..hasNoDeleteBtn',value:false})
// 
learun.set({path:'id..disabled',value:true})
// 
learun.set({path:'id..disabled',value:true})
learun.set({path:'id..abledList',value:['id']})


// 
learun.loading('')
// 
learun.hideLoading('')

// 
learun.message('')

// 
var loginUser = learun.loginUser

// get
// 
var getCallback = function(res){ console.log(res)// }
learun.httpGet({url:api,params:{},getCallback})

// post
// 
var postCallback = function(res){ console.log(res)// }
learun.httpPost({url:api,params:{},data:{},postCallback})

// put
// 
var putCallback = function(res){ console.log(res)// }
learun.httpPut({url:api,params:{},data:{},putCallback})

// post
// 
var deleteCallback = function(res){ console.log(res)// }
learun.httpDelete({url:api,params:{},data:{},deleteCallback})

// 
return 'callback'

            `,
    };
  },
  computed: {
    tabList() {
      return this.formDesign.formInfo.tabList;
    },
    formInfo() {
      return this.formDesign.formInfo;
    },
    isNoScript() {
      return this.formDesign.isNoScript;
    },
    componentOptions() {
      const res = [];
      if (this.formDesign) {
        const formInfo = this.formDesign.configToSaveInfo;
        formInfo.tabList.forEach((tab) => {
          tab.components.forEach((com) => {
            if (
              ![
                "viewtable",
                "card",
                "btn",
                "tab",
                "collapse",
                "divider",
                "lable",
                "gridtable",
                "upload",
                "uploadimg",
              ].includes(com.type) &&
              !com.isNotSave &&
              com.table
            ) {
              res.push({ value: com.prop, label: com.label, table: com.table });
            }
          });
        });
      }
      return res;
    },
  },
  methods: {
    handleAddTabs() {
      this.tabList.push({
        components: [],
        text: this.$t("") + this.tabList.length,
      });
    },
    handleRemoveTabs(index) {
      if (`tab${index}` == this.formDesign.formActiveName) {
        this.formDesign.formActiveName = "tab0";
      }
      this.tabList.splice(index, 1);
    },

    handleBtnClick(type, title) {
      this.type = type;
      this.formTitle = title;
      this.formVisible = true;
    },

    getEvent(strEvent) {
      // 
      if (!this.$validatenull(strEvent)) {
        if (strEvent.indexOf("=>") != -1) {
          // 
          console.warn("ES6ES5");
          return { res: false, msg: "" };
        } else {
          strEvent = `(function(){function fn(learun) {${strEvent}} return fn})()`;
          return this.$getFunction(strEvent);
        }
      } else {
        return { res: true };
      }
    },

    handleCodeSave() {
      const { res, msg } = this.getEvent(this.code);
      if (res) {
        this.formInfo[this.type] = this.code;
        this.formVisible = false;
      } else {
        this.$message({
          type: "error",
          message: `${this.$t("")}:${msg}`,
        });
      }
    },
    handleOpenedForm() {
      this.code = this.formInfo[this.type];
    },
    handleHint(text) {
      text = text.replace(/'/g, "");
      text = text.replace(/"/g, "");
      const list = this.formDesign.componentList.filter(
        (t) => t.label.indexOf(text) != -1 || t.prop.indexOf(text) != -1
      );
      return list.map((t) => `/*${t.label}*/"${t.prop}"`);
    },

    handleAddVRepeatList() {
      // 
      if (this.formInfo.vRepeatList == undefined) {
        this.$set(this.formInfo, "vRepeatList", []);
      }
      this.formInfo.vRepeatList.push({
        value: "",
      });
    },
    handleRemoveVRepeatList(index) {
      this.formInfo.vRepeatList.splice(index, 1);
    },
    vRepeatOptions(index) {
      let selectValue = "";
      let table = "";
      if (this.formInfo.vRepeatList) {
        this.formInfo.vRepeatList.forEach((t, i) => {
          if (t.value && i != index) {
            selectValue += `${t.value},`;
          } else if (t.value && i == index) {
            const vItem = this.componentOptions.find(
              (t2) => t2.value == t.value.split(",")[0]
            );
            if (vItem) {
              table = vItem.table;
            }
          }
        });
      }
      return this.componentOptions.filter(
        (t) =>
          selectValue.indexOf(t.value) == -1 && (!table || table == t.table)
      );
    },
  },
};
</script>

