<template>
  <div class="component-config">
    <el-form
      v-if="this.config && Object.keys(this.config).length > 0"
      label-width="88px"
      label-position="left"
      size="mini"
    >
      <el-form-item label="ID">
        <el-input readonly="readonly" v-model="config.prop"></el-input>
      </el-form-item>
      <el-form-item
        v-if="['input','count'].includes(config.type)"
        :label="$t('')"
      >
        <el-switch
          v-model="config.isNotSave"
          @change="notSaveChange"
        ></el-switch>
      </el-form-item>
      <el-form-item
        :label="$t('')"
        v-if="!config.isNotSave && hasTableSetting && !isAutoCreateTable"
      >
        <el-select
          v-model="config.table"
          :placeholder="$t('')"
          @change="handleTableChange"
        >
          <el-option
            v-for="item in dbTables"
            :key="item.name"
            :label="item.name"
            :value="item.name"
          >
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item
        :label="$t('')"
        v-if="
          ![
            'gridtable',
            'divider',
            'lable',
            'viewtable',
            'card',
            'collapse',
            'tab',
            'btn',
            'qrcode',
            'barcode',
            'tableLayout',
            'tableLayoutTh',
            'tableLayoutTd'
          ].includes(config.type) &&
          !config.isNotSave &&
          !isAutoCreateTable
        "
      >
        <l-select
          :options="getFields(config.table)"
          v-model="config.field"
          @change="handleFiledChange"
        >
        </l-select>
      </el-form-item>
      <el-form-item
        :label="$t('')"
        v-if="
          ![
            'gridtable',
            'divider',
            'lable',
            'viewtable',
            'card',
            'collapse',
            'tab',
            'btn',
            'areaselect',
            'layerbmap',
            'rate',
            'switch',
            'slider',
            'qrcode',
            'barcode',
            'tableLayout',
            'count'
          ].includes(config.type) &&
          !config.isNotSave &&
          isAutoCreateTable
        "
      >
        <l-select
          :options="getTypeOption(config.type)"
          v-model="config.type"
          @change="handleTypeChange"
        >
        </l-select>
      </el-form-item>

      <el-form-item
        :label="$t('')"
        v-if="
          ![
            'gridtable',
            'divider',
            'lable',
            'viewtable',
            'card',
            'collapse',
            'tab',
            'btn',
            'tableLayout',
            'tableLayoutTh',
            'tableLayoutTd'
          ].includes(config.type) && !config.subfield
        "
      >
        <el-switch
          v-model="config.notLabel"
        />
      </el-form-item>

      <el-form-item
        :label="config.type == 'btn' ? $t('') : $t('')"
        v-if="!['divider', 'lable', 'tab', 'collapse','tableLayout','tableLayoutTh','tableLayoutTd'].includes(config.type)"
      >
        <el-input v-model="config.label" :placeholder="$t('')"></el-input>
      </el-form-item>
      <el-form-item
        :label="config.subfield ? $t('') : $t('')"
        v-if="
          ![
            'gridtable',
            'divider',
            'lable',
            'viewtable',
            'card',
            'collapse',
            'tab',
            'btn',
            'tableLayout',
            'tableLayoutTh',
            'tableLayoutTd'
          ].includes(config.type)
        "
      >
        <el-input-number
          v-model="config.labelWidth"
          controls-position="right"
          :placeholder="$t('')"
          :min="0"
        ></el-input-number>
      </el-form-item>

      <el-form-item
        :label="config.subfield ? $t('') : ''"
        v-if="['btn'].includes(config.type) && config.subfield"
      >
        <el-input-number
          v-model="config.labelWidth"
          controls-position="right"
          :placeholder="$t('')"
          :min="0"
        ></el-input-number>
      </el-form-item>
      <el-form-item
        :label="$t('')"
        v-if="
          ![
            'btn',
            'card',
            'collapse',
            'tab',
            'gridtable',
            'divider',
            'lable',
            'radio',
            'timerange',
            'datetimerange',
            'upload',
            'uploadimg',
            'guid',
            'rate',
            'company',
            'department',
            'modifytime',
            'modifyuser',
            'createtime',
            'createuser',
            'viewtable',
            'qrcode',
            'barcode',
            'secretLevel',
            'tableLayout',
            'tableLayoutTh',
            'tableLayoutTd'
          ].includes(config.type)
        "
      >
        <el-input
          v-model="config.placeholder"
          :placeholder="$t('')"
        ></el-input>
      </el-form-item>
      <el-form-item
        :label="$t('')"
        v-if="['timerange', 'datetimerange'].includes(config.type)"
      >
        <el-input
          v-model="config.startPlaceholder"
          :placeholder="$t('')"
        ></el-input>
      </el-form-item>
      <el-form-item
        :label="$t('')"
        v-if="['timerange', 'datetimerange'].includes(config.type)"
      >
        <el-input
          v-model="config.endPlaceholder"
          :placeholder="$t('')"
        ></el-input>
      </el-form-item>
      <el-form-item
        :label="$t('')"
        v-if="
          !config.subfield &&
          !['gridtable', 'texteditor', 'viewtable', 'qrcode', 'barcode','tableLayout','tableLayoutTh','tableLayoutTd'].includes(config.type)
        "
      >
        <el-slider
          v-model="config.span"
          :step="6"
          :min="6"
          :max="24"
          show-stops
        >
        </el-slider>
      </el-form-item>

      <component :is="getComponent" :data="config" :isAutoCreateTable="isAutoCreateTable"></component>

      <template
        v-if="(!config.subfield && !isNoScript) || (config.type == 'btn' && !isNoScript)"
      >
        <el-divider
          v-if="
            [
              'btn',
              'radio',
              'checkbox',
              'select',
              'selectMultiple',
              'treeselect',
              'layerselect',
              'areaselect',
              'companySelect',
              'departmentSelect',
              'userSelect',
              'switch',
              'count'
            ].includes(config.type)
          "
        >
          {{ $t("") }}
        </el-divider>
        <div
          class="mt-8"
          v-if="
            [
              'radio',
              'checkbox',
              'select',
              'selectMultiple',
              'treeselect',
              'layerselect',
              'areaselect',
              'companySelect',
              'departmentSelect',
              'userSelect',
              'switch',
              'count'
            ].includes(config.type)
          "
        >
          <el-button
            @click="handleBtnClick('changeCode', '')"
            type="primary"
            size="mini"
            >{{ $t("") }}</el-button
          >
        </div>
        <div class="mt-8" v-else-if="['btn'].includes(config.type)">
          <el-button
            @click="handleBtnClick('clickCode', '')"
            type="primary"
            size="mini"
            >{{ $t("") }}</el-button
          >
        </div>
      </template>
    </el-form>
    <div
      v-else
      style="width: 100%; text-align: center; margin-top: 100%; color: #909399"
    >
      {{ $t("") }}!
    </div>

    <l-dialog
      :title="$t(formTitle)"
      :visible.sync="formVisible"
      :height="528"
      :width="1000"
      @ok="handleCodeSave"
      @opened="handleOpenedForm"
    >
      <l-layout :right="400">
        <l-code-mirror
          v-model="code"
          isHint
          :handleHint="handleHint"
          mode="application/javascript"
        ></l-code-mirror>
        <template #right>
          <l-code-mirror readOnly v-model="tempCode"> </l-code-mirror>
        </template>
      </l-layout>
    </l-dialog>
  </div>
</template>

<script>
const requireComponent = require.context("./", true, /\.vue$/);
const myComponents = {};
requireComponent.keys().map((fileName) => {
  const name = fileName.split("/")[1].split(".")[0];
  if (name != "cascader") {
    myComponents[`config-${name}`] = requireComponent(fileName).default;
  }
});

export default {
  name: "component-config",
  inject: ["formDesign"],
  props: ["data", "isAutoCreateTable"],
  components: {
    ...myComponents,
  },
  computed: {
    config() {
      return this.data;
    },
    getComponent() {
      const prefix = "config-";
      const { type } = this.config;
      return prefix + type;
    },
    dbTables() {
      if (this.config.type == "gridtable") {
        return this.formDesign.dbTables.filter((t) => t.type == "chlid");
      } else {
        return this.formDesign.dbTables;
      }
    },
    hasTableSetting() {
      return (
        !this.config.subfield &&
        ![
          "divider",
          "lable",
          "viewtable",
          "card",
          "tab",
          "collapse",
          "btn",
          'qrcode',
          'barcode',
          'tableLayout',
          'tableLayoutTh',
          'tableLayoutTd'
        ].includes(this.config.type)
      ); //&& this.dbTables.length > 1
    },
    isNoScript() {
      return this.formDesign.isNoScript;
    },
  },
  data() {
    return {
      formVisible: false,
      type: "",
      formTitle: "",
      code: "",
      tempCode: `
// ,ES5
// 
var isUpdate = learun.isUpdate

// id
var id = learun.prop
// 
var data = learun.data
//  rowIndex
var rowIndex = learun.rowIndex

// 
var value = learun.get('id')
// 
var childRow = learun.get('id.')
// 
var childCell = learun.get('id..id')

// 
learun.set({path:'id',value:'xxxx'})
// 
var row = {}
learun.set({path:'id',value:row,type:'addTable'})
// 
learun.set({path:'id.',type:'deleteTable'})
// 
learun.set({path:'id..id',value:'xxxxxx'})

// 
var label = learun.getLabel('id')
// 
var cellLabel =  learun.getLabel('id..id')

// 
learun.setRequired('id')
// 
learun.setRequired('id',false)

// 
learun.setDisabled('id')
// 
learun.setDisabled('id',false)

// 
learun.setHide('id')
// 
learun.setHide('id',false)

// 
learun.set({path:'id..hasNoDeleteBtn',value:false})
// 
learun.set({path:'id..disabled',value:true})
// 
learun.set({path:'id..disabled',value:true})
learun.set({path:'id..abledList',value:['id']})


// 
learun.loading('')
// 
learun.hideLoading('')

// 
learun.message('')

// 
var loginUser = learun.loginUser

// get
// 
var getCallback = function(res){ console.log(res)// }
learun.httpGet({url:api,params:{},getCallback})

// post
// 
var postCallback = function(res){ console.log(res)// }
learun.httpPost({url:api,params:{},data:{},postCallback})

// put
// 
var putCallback = function(res){ console.log(res)// }
learun.httpPut({url:api,params:{},data:{},putCallback})

// post
// 
var deleteCallback = function(res){ console.log(res)// }
learun.httpDelete({url:api,params:{},data:{},deleteCallback})

// 
return 'callback'

            `,
    };
  },
  methods: {
    notSaveChange(val) {
      if (val) {
        this.config.readonly = true;
        this.config.display = true;
      }
    },
    handleTableChange(val) {
      if (this.config.type == "gridtable" && this.config.children) {
        this.config.children.forEach((item) => {
          item.table = val;
        });
      }
    },
    getFields(tableName) {
      let table =
        this.formDesign.dbTables.find((t) => t.name == tableName) || {};
      const columns = table.columns || [];
      return columns.map((t) => ({
        ...t,
        value: t.name,
        label: t.coment ? `${t.name}(${t.coment})` : t.name,
      }));
    },
    handleFiledChange(obj) {
      if (obj) {
        this.config.csType = obj.csType;
        if (["switch"].includes(this.config.type)) {
          switch (this.config.csType) {
            case "int":
              this.config.activeValue = "1";
              this.config.inactiveValue = "0";
              this.config.valueType = "number";
              break;
            case "bool":
              this.config.activeValue = "true";
              this.config.inactiveValue = "false";
              this.config.valueType = "boolean";
              break;
            default:
              this.config.valueType = "string";
              break;
          }
        }

        if (obj.coment) {
          this.config.label = obj.coment;
        }
      } else {
        this.config.csType = "";
      }

      //console.log(obj,'')
    },

    handleBtnClick(type, title) {
      this.type = type;
      this.formTitle = title;
      this.formVisible = true;
    },
    getEvent(strEvent) {
      // 
      if (!this.$validatenull(strEvent)) {
        if (strEvent.indexOf("=>") != -1) {
          // 
          console.warn("ES6ES5");
          return { res: false, msg: "" };
        } else {
          strEvent = `(function(){function fn(learun) {${strEvent}} return fn})()`;
          return this.$getFunction(strEvent);
        }
      } else {
        return { res: true };
      }
    },
    handleCodeSave() {
      const { res, msg } = this.getEvent(this.code);
      if (res) {
        this.data[this.type] = this.code;
        this.formVisible = false;
      } else {
        this.$message({
          type: "error",
          message: `:${msg}`,
        });
      }
    },
    handleOpenedForm() {
      this.code = this.data[this.type] || "";
    },
    handleHint(text) {
      text = text.replace(/'/g, "");
      text = text.replace(/"/g, "");
      const list = this.formDesign.componentList.filter(
        (t) => t.label.indexOf(text) != -1 || t.prop.indexOf(text) != -1
      );
      return list.map((t) => `/*${t.label}*/"${t.prop}"`);
    },
    getTypeOption(type) {
      switch (type) {
        case "input":
        case "textarea":
        case "texteditor":
        case "number":
        case "password":
        case "icon":
        case "color":
        case "encode":
          return [
            { value: "input", label: "" },
            { value: "textarea", label: "" },
            { value: "texteditor", label: "" },
            { value: "number", label: "" },
            { value: "password", label: "" },
            { value: "icon", label: "" },
            { value: "color", label: "" },
            { value: "encode", label: "" },
          ];
        case "radio":
        case "checkbox":
        case "select":
        case "selectMultiple":
        case "treeselect":
        case "layerselect":
          return [
            { value: "radio", label: "" },
            { value: "checkbox", label: "" },
            { value: "select", label: "" },
            { value: "selectMultiple", label: "" },
            { value: "treeselect", label: "" },
            { value: "layerselect", label: "" },
          ];
        case "upload":
        case "uploadimg":
          return [
            { value: "upload", label: "" },
            { value: "uploadimg", label: "" },
          ];
        case "guid":
        case "companySelect":
        case "departmentSelect":
        case "userSelect":
        case "company":
        case "department":
        case "createuser":
        case "modifyuser":
          return [
            { value: "guid", label: "GUID" },
            { value: "companySelect", label: "" },
            { value: "departmentSelect", label: "" },
            { value: "userSelect", label: "" },
            { value: "company", label: "" },
            { value: "department", label: "" },
            { value: "createuser", label: "" },
            { value: "modifyuser", label: "" },
          ];
        case "datetime":
        case "createtime":
        case "modifytime":
          return [
            { value: "datetime", label: "" },
            { value: "createtime", label: "" },
            { value: "modifytime", label: "" },
          ];
        case "time":
        case "timerange":
        case "datetimerange":
          return [
            { value: "time", label: "" },
            { value: "timerange", label: "" },
            { value: "datetimerange", label: "" },
          ];
      }
    },
    handleTypeChange(obj) {
      this.config.label = obj.label;
      this.$set(this.config, "default", "");
      if (
        [
          "radio",
          "checkbox",
          "select",
          "selectMultiple",
          "treeselect",
          "layerselect",
        ].includes(obj.value)
      ) {
        if (obj.value == "selectMultiple") {
          this.$set(this.config, "multiple", true);
        } else this.$set(this.config, "multiple", false);
        if (obj.value == "layerselect") {
          this.$set(this.config, "columns", []);
          this.$set(this.config, "isPage", true);
          this.$set(this.config, "height", 504);
          this.$set(this.config, "width", 960);
          this.config.dataType = "2";
        } else {
          this.config.dataType = "1";
        }
        if (obj.value == "treeselect") {
          this.config.dataIdKey = "";
          this.config.dataPIdKey = "";
          this.config.options = [
            {
              label: "",
              value: "0",
              children: [
                {
                  label: "1-1",
                  value: "11",
                },
                {
                  label: "1-2",
                  value: "12",
                },
              ],
            },
            { label: "", value: "1" },
            { label: "", value: "2" },
          ];
        } else if (
          ["radio", "checkbox", "select", "selectMultiple"].includes(obj.value)
        ) {
          this.config.options = [
            {
              value: "1",
              label: "1",
            },
            {
              value: "2",
              label: "2",
            },
            {
              value: "3",
              label: "3",
            },
          ];
        }
      } else if (["upload", "uploadimg"].includes(obj.value)) {
        if (["uploadimg"].includes(obj.value)) {
          this.config.listType = "picture-card";
        } else this.config.listType = "";
      } else if (
        ["companySelect", "departmentSelect", "userSelect", "encode"].includes(
          obj.value
        )
      ) {
        if (["companySelect", "departmentSelect"].includes(obj.value)) {
          this.config.isLogin = false;
        } else if (["userSelect"].includes(obj.value)) {
          this.config.isLogin = false;
          this.config.multiple = false;
        } else {
          this.config.code = "";
          this.config.display = true;
        }
      } else if (["datetime"].includes(obj.value)) {
        this.$set(this.config, "format", "yyyy-MM-dd");
        this.$set(this.config, "placeholder", "");
        this.$set(this.config, "isRange", false);
        this.$set(this.config, "dateType", 'date');
      } else if (["datetimerange"].includes(obj.value)) {
        this.$set(this.config, "format", "yyyy-MM-dd");
        this.$set(this.config, "startPlaceholder", "");
        this.$set(this.config, "endPlaceholder", "");
        this.$set(this.config, "isRange", true);
        this.$set(this.config, "dateType", 'daterange');
      } else if (["time"].includes(obj.value)) {
        this.$set(this.config, "format", "HH:mm:ss");
        this.$set(this.config, "selectableRange", "");
        this.$set(this.config, "isRange", false);
        this.$set(this.config, "dateType", '');
      } else if (["timerange"].includes(obj.value)) {
        this.$set(this.config, "format", "HH:mm:ss");
        this.$set(this.config, "startPlaceholder", "");
        this.$set(this.config, "endPlaceholder", "");
        this.$set(this.config, "isRange", true);
        this.$set(this.config, "dateType", '');
        this.$set(this.config, "selectableRange", "");
      }
    },
  },
};
</script>
