<template>
  <div class="config_count">
    <el-form-item :label="$t('')">
      <el-input-number
        v-model="data.decimal"
        controls-position="right"
        :placeholder="$t('')"
        :min="0"
      ></el-input-number>
    </el-form-item>
    <el-form-item :label="$t('')">
      <el-switch
        v-model="data.thousandSeparator"
        @change="handleChangeTS"
      ></el-switch>
    </el-form-item>
    <el-form-item :label="$t('')">
      <el-switch v-model="data.isChinese" @change="handleChangeCap"></el-switch>
    </el-form-item>
    <el-form-item :label="$t('')">
      <el-switch v-model="data.display"></el-switch>
    </el-form-item>
    <el-divider>{{ $t("") }}</el-divider>
    <div class="mt-8">
      <el-button @click="handleCountSet" type="primary" size="mini">{{
        $t("")
      }}</el-button>
    </div>
    <!-- <el-divider>{{ $t("") }}</el-divider>
    <div class="mt-8">
      <el-button @click="handleLinkageSet" type="primary" size="mini">{{
        $t("")
      }}</el-button>
    </div> -->
    <l-dialog
      :title="$t('')"
      :visible.sync="formVisible"
      :height="720"
      :width="1000"
      @ok="handleSave"
      @open="handleOpen"
      @close="handleClose"
    >
      <div style="padding: 16px" class="box">
        <div class="layTop">
          <div class="l-rblock top_box">
            <div>
              <span
                ><span class="blod">{{ data.label }}=</span></span
              >
              <span class="clear_style1" @click="handleClear"></span>
              <span class="clear_style2" @click="handleCallBack"></span>
              <i
                class="el-icon-right clear_style2 margint_4"
                @click="handleRight"
              ></i>
              <i
                class="el-icon-back clear_style2 margint_4"
                @click="handleBack"
              ></i>
            </div>
            <div class="code_box">
              <span class="code_cursor" v-if="formatJson.cursor == 0"></span>
              <span v-for="(item, index) in formatCells" :key="index">
                <span class="fun_color" v-if="item.type == 'fun'">
                  {{ item.name.toUpperCase() }}
                </span>
                <span class="comp_color" v-if="item.type == 'comp'">
                  #{{ item.tableId ? handleGetLabel(item.tableId) : "" }}.{{
                    handleGetLabel(item.name)
                  }}#
                </span>
                <span class="num_color" v-if="item.type == 'num'">
                  {{ item.name }}
                </span>
                <span
                  class="code_cursor"
                  v-if="index + 1 == formatJson.cursor"
                ></span>
              </span>
            </div>
          </div>
        </div>
        <div class="center_box">
          <div
            v-for="(item, index) in numList"
            :key="index"
            class="border_color num_wh"
            @click="handleClick(item, 'num')"
          >
            {{ item.label }}
          </div>
        </div>
        <div class="layBottom">
          <div class="down_box">
            <div class="bottom_left">
              <p class="p1"></p>
              <div class="coms_box">
                <div
                  v-for="(item, index) in valueOptions.mainList"
                  :key="index"
                  class="border_color"
                  @click="handleClick(item, 'comp')"
                >
                  {{ item.label }}
                </div>
              </div>
              <p class="p1 margin_top0"></p>
              <div
                v-for="(item, index) in valueOptions.gridtableList"
                :key="index"
              >
                <p class="p3 margin_top0">{{ item.label }}</p>
                <div class="coms_box">
                  <div
                    v-for="(t, i) in item.coms"
                    :key="i"
                    class="border_color"
                    @click="handleClick(t, 'comp', item.value)"
                  >
                    {{ t.label }}
                  </div>
                </div>
              </div>
            </div>
            <div class="bottom_right">
              <div class="right1">
                <p class="p1"></p>
                <p
                  class="p2"
                  v-for="item in funList"
                  :key="item.value"
                  @mouseover="handleMouseover(item.hopeList)"
                  @click="handleClick(item, 'fun')"
                >
                  {{ item.label }}
                </p>
              </div>
              <ul class="right2">
                <li v-for="(item, index) in hopeList" :key="index">
                  {{ item }}
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </l-dialog>
  </div>
</template>

<script>
export default {
  name: "config-count",
  props: ["data"],
  inject: ["formDesign"],
  data() {
    return {
      formVisible: false,
      funList: [
        {
          label: "ABS",
          value: "abs",
          hopeList: [
            "ABS",
            "ABS()",
            "ABS(-8)8-8",
          ],
        },
        {
          label: "AVERAGE",
          value: "average",
          hopeList: [
            "AVERAGE",
            "AVERAGE(1,2,...)",
            "AVERAGE({},{}, {})",
          ],
        },
        {
          label: "CEILING",
          value: "ceiling",
          hopeList: [
            "CEILING",
            "CEILING(,)",
            "CEILING(7,6)1212767",
          ],
        },
        {
          label: "COUNT",
          value: "count",
          hopeList: [
            "COUNT",
            "COUNT(,,...)",
            "COUNT(,,,)4",
          ],
        },
        {
          label: "COUNTIF",
          value: "countif",
          hopeList: [
            "COUNTIF",
            "COUNTIF(,'')",
            "COUNTIF(., '')''COUNTIF([1,2,3,4],'>2')1,2,3,422",
          ],
        },
        {
          label: "FIXED",
          value: "fixed",
          hopeList: [
            "FIXED",
            "FIXED(,)",
            "FIXED(3.1415,2)'3.14'",
          ],
        },
        {
          label: "FLOOR",
          value: "floor",
          hopeList: [
            "FLOOR",
            "FLOOR(,)",
            "FLOOR(7,6)66767",
          ],
        },
        {
          label: "INT",
          value: "int",
          hopeList: [
            "INT",
            "INT()",
            "INT(3.1415)33.1415",
          ],
        },
        {
          label: "LARGE",
          value: "large",
          hopeList: [
            "LARGEk",
            "LARGE(,k)",
            "LARGE({.},1)''1''",
          ],
        },
        {
          label: "LOG",
          value: "log",
          hopeList: [
            "LOG",
            "LOG(,)",
            "LOG(100,10)210100",
          ],
        },
        {
          label: "MAX",
          value: "max",
          hopeList: [
            "MAX",
            "MAX(1,2,...)",
            "MAX({},{},{})",
          ],
        },
        {
          label: "MIN",
          value: "min",
          hopeList: [
            "MIN",
            "MIN(1,2,...)",
            "MIN({},{},{})",
          ],
        },
        {
          label: "MOD",
          value: "mod",
          hopeList: [
            "MOD",
            "MOD(,)",
            "MOD(4,3)14/3",
          ],
        },
        {
          label: "POWER",
          value: "power",
          hopeList: [
            "POWER",
            "POWER(,)",
            "POWER(32)932",
          ],
        },
        {
          label: "PRODUCT",
          value: "product",
          hopeList: [
            "PRODUCT",
            "PRODUCT(1,2,...)",
            "PRODUCT({}, {})",
          ],
        },
        {
          label: "RAND",
          value: "rand",
          hopeList: [
            "RAND01",
            "RAND()",
            "RAND()0.424656",
          ],
        },
        {
          label: "ROUND",
          value: "round",
          hopeList: [
            "ROUND",
            "ROUND(,)",
            "ROUND(3.1485,2)3.15",
          ],
        },
        {
          label: "SMALL",
          value: "small",
          hopeList: [
            "SMALLk",
            "SMALL(,k)",
            "SMALL({.}, 1)''''",
          ],
        },
        {
          label: "SQRT",
          value: "sqrt",
          hopeList: [
            "SQRT",
            "SQRT()",
            "SQRT(9)39",
          ],
        },
        {
          label: "SUM",
          value: "sum",
          hopeList: [
            "SUM",
            "SUM(1,2,...)",
            "SUM({},{}, {})",
          ],
        },
        {
          label: "SUMPRODUCT",
          value: "sumproduct",
          hopeList: [
            "SUMPRODUCT",
            "SUMPRODUCT(,...)",
            "SUMPRODUCT([1,2,3],[0.1,0.2,0.3])1.4 10.1 + 20.2 + 30.3",
          ],
        },
      ],
      hopeList: [],
      numList: [
        { label: 1, value: 1 },
        { label: 2, value: 2 },
        { label: 3, value: 3 },
        { label: 4, value: 4 },
        { label: 5, value: 5 },
        { label: 6, value: 6 },
        { label: 7, value: 7 },
        { label: 8, value: 8 },
        { label: 9, value: 9 },
        { label: 0, value: 0 },
        { label: "+", value: "+" },
        { label: "-", value: "-" },
        { label: "*", value: "*" },
        { label: "/", value: "/" },
        { label: ">", value: ">" },
        { label: "<", value: "<" },
        { label: "=", value: "=" },
        { label: "(", value: "(" },
        { label: ")", value: ")" },
        { label: "[", value: "[" },
        { label: "]", value: "]" },
        { label: "'", value: "'" },
        { label: `"`, value: `"` },
        { label: ",", value: "," },
        { label: ".", value: "." },
      ],
      formatJson: {
        cells: [], // (),
        cursor: 0,
      },
    };
  },
  computed: {
    formatCells() {
      return this.formatJson.cells;
    },
    valueOptions() {
      const res = {
        mainList: [],
        gridtableList: [],
      };
      const formInfo = this.formDesign.toSaveData();
      formInfo.tabList.forEach((tab) => {
        tab.components.forEach((com) => {
          if (
            ![
              "viewtable",
              "card",
              "btn",
              "tab",
              "collapse",
              "divider",
              "gridtable",
              "btn",
              "qrcode",
              "barcode",
              "tableLayout",
              "time",
              "timerange",
              "datetime",
              "datetimerange",
              "areaselect",
              "layerbmap",
              "upload",
              "uploadimg",
              "guid",
              "companySelect",
              "departmentSelect",
              "userSelect",
              "company",
              "department",
              "modifytime",
              "modifyuser",
              "createtime",
              "createuser",
              "icon",
              "switch",
              "color",
            ].includes(com.type) &&
            com.prop != this.data.prop
          ) {
            res.mainList.push({ value: com.prop, label: com.label });
          } else if (["gridtable"].includes(com.type)) {
            let comsList = [];
            com.children.forEach((c) => {
              if (
                c.prop != this.data.prop &&
                ![
                  "time",
                  "timerange",
                  "datetime",
                  "datetimerange",
                  "areaselect",
                  "layerbmap",
                  "upload",
                  "uploadimg",
                  "guid",
                  "companySelect",
                  "departmentSelect",
                  "userSelect",
                  "company",
                  "department",
                  "modifytime",
                  "modifyuser",
                  "createtime",
                  "createuser",
                  "icon",
                  "switch",
                  "color",
                ].includes(c.type)
              ) {
                comsList.push({ value: c.prop, label: c.label });
              }
            });
            res.gridtableList.push({
              value: com.prop,
              label: com.label,
              coms: comsList,
            });
          }
        });
      });
      return res;
    },
    allComps() {
      let res = [];
      res = this.$deepClone(this.valueOptions.mainList);
      this.valueOptions.gridtableList.map((item) => {
        res.push({ value: item.value, label: item.label });
        res.push(...item.coms);
      });
      return res;
    },
  },
  methods: {
    handleCountSet() {
      this.formVisible = true;
    },
    handleLinkageSet() {},
    handleSave() {
      this.data.formatJson = this.formatCells;
      this.formVisible = false;
    },
    handleOpen() {
      if (this.data.formatJson.length)
        this.formatJson.cells = this.$deepClone(this.data.formatJson);
      this.formatJson.cursor = this.formatJson.cells.length;
    },
    handleClose() {
      this.handleClear();
      this.formVisible = false;
    },
    handleMouseover(val) {
      this.hopeList = val;
    },
    handleClick(item, type, value) {
      let cell = {
        name: item.value,
        type,
      };
      if (value) cell.tableId = value;
      this.formatCells.splice(this.formatJson.cursor, 0, cell);
      this.formatJson.cursor += 1;
      if (type === "fun") {
        let leftBracket = {
          name: "(",
          type,
        };
        let rightBracket = {
          name: ")",
          type,
        };
        this.formatCells.splice(
          this.formatJson.cursor,
          0,
          leftBracket,
          rightBracket
        );
        this.formatJson.cursor += 1;
      }
    },
    handleGetLabel(prop) {
      let list = this.allComps.filter((item) => item.value == prop);
      return list[0].label;
    },
    handleBack() {
      if (this.formatJson.cursor) {
        this.formatJson.cursor -= 1;
      }
    },
    handleRight() {
      if (this.formatJson.cursor < this.formatCells.length) {
        this.formatJson.cursor += 1;
      }
    },
    handleCallBack() {
      if (this.formatJson.cursor > 0) {
        this.formatCells.splice(this.formatJson.cursor - 1, 1);
        this.formatJson.cursor -= 1;
      }
    },
    handleClear() {
      this.formatJson = {
        cells: [],
        cursor: 0,
      };
    },
    handleChangeTS(val) {
      if (val) this.$set(this.data, "isChinese", false);
    },
    handleChangeCap(val) {
      if (val) this.$set(this.data, "thousandSeparator", false);
    },
  },
};
</script>

